const state = {
    'login_info': {},
}
const mutations = {
    SET_Login: (state, param) => {
        state.login_info = param
    },
    CLEAR_Login: (state) => {
        state.login_info = {}
    },
}

const actions = {
    setInfo({ commit }, param) {
        commit('SET_Login', param)
    },
    clearInfo({ commit }) {
        commit('CLEAR_Login')
    },
}

export default { state, mutations, actions }


