export default {
  name: "goodlist",
  props: {
    goodList: {
      type: Array,
      default: null
    },
    isTitle: {
      type: [Boolean, String],
      default: true
    }
  },
  components: {},
  computed: {
    // 计算属性，过滤当前列表数据，金额从分转成元，获取券后价格
    goodListFormat() {
      return this.goodList.map(item => {
        return {
          ...item,
        };
      });
    }
  },
  data() {
    return {};
  },
  methods: {
    seeDetail(item) {
      this.$router.push({
        path: "/taobao/taobao_detail",
        query: {
          goods_id:item.item_id, //向详情页传递good_id
          coupon_id:item.coupon_id, //向详情页传递coupon_id
          url:item.coupon_share_url?'https:' + item.coupon_share_url :'https:' +item.url, //向详情页传递url
        }
      });
    }
  }
};



