
import Search from 'vux/src/components/search/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import goodlist from "@/components/pddgoodlist";
import sortbar from "@/components/sortbar";

export default {
name: "pdd_search",
components: {
Search,
Grid,
GridItem,
goodlist,
sortbar
},
computed: {},

data() {
return {
keyword: null,
barHeight: null,
goodList: [],
sortSelected: {
sortSelected: 0,
sort_type: 0,
with_coupon: false
},
sortbar: [
{
name: "综合",
type: 0,
className: "sort",
value: 0,
sort_id: 0
},
{
name: "销量",
type: 1,
className: "sort",
value: 1,
choose: 0,
sort_id: [5, 6]
},
{
name: "价格",
type: 1,
className: "sort",
value: 2,
choose: 0,
sort_id: [3, 4]
},
{
name: "优惠券",
type: 0,
className: "with_coupon",
value: 3
}
],
page: 1
};
},
methods: {
search(value) {
this.keyword = value;
(this.page = 1), (this.goodList = []);
this.pddDdkGoodsSearch({
keyword: this.keyword,
sort_type: this.sortSelected.sort_type,
with_coupon: this.sortSelected.with_coupon
});
},
b() {},

changesort(value) {
if (!this.keyword) {
this.$vux.toast.show({
type: "text",
text: "请输入您要搜索的商品名"
});
} else {
this.sortSelected.sortSelected = value.value; 
(this.page = 1), (this.goodList = []);
if (value.type == 1) {
this.sortbar[value.index].choose = value.choose + 1;
this.sortSelected.sort_type =
value.sort_id[this.sortbar[value.index].choose % 2];
} else {
this.sortSelected.sort_type = value.sort_id;
}
this.pddDdkGoodsSearch({
keyword: this.keyword,
sort_type: this.sortSelected.sort_type,
with_coupon: this.sortSelected.with_coupon
});
}
},

changecoupon(value) {
this.sortSelected.with_coupon = !this.sortSelected.with_coupon;
(this.page = 1), (this.goodList = []);
this.pddDdkGoodsSearch({
keyword: this.keyword,
sort_type: this.sortSelected.sort_type,
with_coupon: this.sortSelected.with_coupon
});
},

onScroll() {

let innerHeight = document.querySelector("#app").clientHeight; 
let outerHeight = document.documentElement.clientHeight; 
let scrollTop = document.documentElement.scrollTop; 
if (outerHeight + scrollTop == innerHeight) {

this.pddDdkGoodsSearch(this.opt_id);
}
},

async pddDdkGoodsSearch(obj) {
var res = await this.$pdd.pddrequest({
...obj,
type: "pdd.ddk.goods.search",
page_size: 20,
page: this.page
});
this.goodList = this.goodList.concat(
res.goods_search_response.goods_list
);
this.page = this.page + 1; 
}
},
async created() {

window.addEventListener("scroll", this.onScroll);
},
async mounted() {

this.barHeight = document.querySelector("#bar-fixed").clientHeight;
}
};



