

import Search from 'vux/src/components/search/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'


import goodlist from "@/components/juhuasuangoodlist";
import { create } from "domain";

export default {
name: "pdd",
components: {
Search,
Grid,
GridItem,
Tab,
TabItem,
Swiper,
goodlist
},
computed: {},
data() {
return {
page: 1,
goodList: [],
barHeight: 0
};
},
methods: {

search() {
this.$router.push({ path: "/taobao/taobao_search" });
},












async taobaoJuItemsSearch() {
this.$vux.loading.show({
text: "正在努力加载..."
});
var obj = {
pid: this.$store.state.pid,
page_size: 20,
current_page: this.page
};
var res = await this.$request.taobao({
param_top_item_query: JSON.stringify(obj),
method: "taobao.ju.items.search"
});
this.goodList = this.goodList.concat(
res.data.ju_items_search_response.result.model_list.items
);
this.page = this.page + 1; 
this.$vux.loading.hide();
}
},
async created() {


},
async mounted() {

this.barHeight = document.querySelector("#bar-fixed").clientHeight;
this.taobaoJuItemsSearch();
this.$vux.toast.show({
type: "text",
text: "聚划算商品，没有优惠券哦~"
});
}
};



