export default {
  name: "goodlist",
  props: {
    config: {
      type: Array,
      default: null
    },
    selected: {
      type: Object,
      default: null
    }
  },
  components: {},
  computed: {},
  data() {
    return {};
  },
  methods: {
    sortbarClick(index, value) {
      value = {
        ...value,
        index
      };
      if (this.selected.sortSelected != value.value || value.type == 1) {
        if (value.className == "sort") {
          this.$emit("sortType", value);
        } else if (value.className == "with_coupon") {
          this.$emit("couponType", value);
        }
      }
    }
  }
};



