

import Vue from 'vue'
import App from './App'
import router from './router/index'
import store from '@/store/store'


import FastClick from 'fastclick'
FastClick.attach(document.body)


import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)


import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin)
import AlertPlugin from 'vux/src/plugins/alert/index.js'

Vue.use(AlertPlugin)
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use( LoadingPlugin  )


import request from './http/post'
Vue.prototype.$request = request


import pdd from './common/pdd'
Vue.prototype.$pdd = pdd


import tb from './common/tb'
Vue.prototype.$tb = tb


import jd from './common/jd'
Vue.prototype.$jd = jd


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')




