
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
name: "app",
components: {
Tabbar,
TabbarItem,
ViewBox
},
computed: {
isshowtabbar() {
if (
this.$route.path == "/pdd" ||
this.$route.path == "/taobao" ||
this.$route.path == "/jingdong"
) {
return true;
} else {
return false;
}
}
},
methods: {},
async mounted() {
var res = await this.$request.get("getuserinfo", "base");

this.$store.dispatch("setInfo", res.data);
if (res.status == -1) {
this.$vux.toast.show({
type: "text",
text: "登录失败，请重试"
});
} else if (res.status == 0) {

}
}
};



