
import Search from 'vux/src/components/search/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import goodlist from "@/components/taobaogoodlist";
import sortbar from "@/components/sortbar";

export default {
name: "pdd_search",
components: {
Search,
Grid,
GridItem,
goodlist,
sortbar
},
computed: {},

data() {
return {
keyword: null,
barHeight: null,
goodList: [],
sortSelected: {
sortSelected: 0,
sort_type: "tk_total_sales_des",
with_coupon: false
},
sortbar: [
{
name: "综合",
type: 0,
className: "sort",
value: 0,
sort_id: "tk_total_sales_des"
},
{
name: "销量",
type: 1,
className: "sort",
value: 1,
choose: 0,
sort_id: ["total_sales_asc", "total_sales_des"]
},
{
name: "价格",
type: 1,
className: "sort",
value: 2,
choose: 0,
sort_id: ["price_asc", "price_des"]
},
{
name: "优惠券",
type: 0,
className: "with_coupon",
value: 3
}
],
page: 1
};
},
methods: {
search(value) {
if (value) {
this.keyword = value;
(this.page = 1), (this.goodList = []);
this.taobaoTbkDgMaterialOptional({
q: this.keyword,
sort: this.sortSelected.sort_type,
has_coupon: this.sortSelected.has_coupon
});
} else {
this.$vux.toast.show({
type: "text",
text: "请输入关键词"
});
}
},
b() {},

changesort(value) {
if (!this.keyword) {
this.$vux.toast.show({
type: "text",
text: "请输入您要搜索的商品名"
});
} else {
this.sortSelected.sortSelected = value.value; 
(this.page = 1), (this.goodList = []);
if (value.type == 1) {
this.sortbar[value.index].choose = value.choose + 1;
this.sortSelected.sort_type =
value.sort_id[this.sortbar[value.index].choose % 2];
} else {
this.sortSelected.sort_type = value.sort_id;
}
this.taobaoTbkDgMaterialOptional({
q: this.keyword,
sort: this.sortSelected.sort_type,
has_coupon: this.sortSelected.with_coupon
});
}
},

changecoupon(value) {
this.sortSelected.with_coupon = !this.sortSelected.with_coupon;
(this.page = 1), (this.goodList = []);
this.taobaoTbkDgMaterialOptional({
q: this.keyword,
sort: this.sortSelected.sort_type,
has_coupon: this.sortSelected.with_coupon
});
},
















loadmore() {
this.taobaoTbkDgMaterialOptional({
q: this.keyword,
sort: this.sortSelected.sort_type,
has_coupon: this.sortSelected.has_coupon
});
},

async taobaoTbkDgMaterialOptional(obj) {
this.$vux.loading.show({
text: "正在努力加载..."
});

var res = await this.$request.taobao({
...obj,
adzone_id: this.$store.state.adzone_id,
method: "taobao.tbk.dg.material.optional",
page_size: 20,
page_no: this.page
});
this.goodList = this.goodList.concat(
res.data.tbk_dg_material_optional_response.result_list.map_data
);
this.page = this.page + 1; 
this.$vux.loading.hide();
}
},
async created() {

window.addEventListener("scroll", this.onScroll);
},
async mounted() {

this.barHeight = document.querySelector("#bar-fixed").clientHeight;
}
};



